<template>
    <div class="home-topbar-wrapper border-half-bottom">
        <div class="top-menu-bar">
            <swiper :options="swiperOption" ref="swiper-box">
                <swiper-slide v-for="(news, index) in newsList" :class="{'active': homeNewsIndex === index}" @click.native="active(index)" :key="index">{{news.title}}</swiper-slide>
            </swiper>
        </div>
        <a class="top-menu-more-btn df-c" href="javascript:void(0)" @click="isTopBarBox=true"><i class="list-shadow"></i><span class="cross"></span></a>

        <TopBarBox :class="{'top-bar-active-active': isTopBarBox}" @close="isTopBarBox=false"></TopBarBox>
        
    </div>
</template>
<script>
    import { swiper, swiperSlide } from 'vue-awesome-swiper'
    import { mapGetters } from 'vuex'
    import TopBarBox from 'components/TopBarBox'
    export default {
        components: {
            swiper, 
            swiperSlide,
            TopBarBox
        },
        data () {
            return{
                swiperOption: {
                    slidesPerView: 6
                },
                isTopBarBox: false
            }
        },
        methods: {
            active (index) {
                this.$store.state.home.newsIndex = index
                this.$store.dispatch('getHomeList', this.newsList[this.homeNewsIndex])
            }
        },
        computed: {
            swiper () {
                return this.$refs['swiper-box'].swiper
            },
            ...mapGetters([
                'newsList',
                'homeNewsIndex'
            ])
        }
    }
</script>
<style lang="less" scoped>
@import '../../../styles/theme.less';

    @bar-color: #f4f5f6;
    .home-topbar-wrapper {
        height: 0.4rem;
        line-height: 0.4rem;
        color: @font-normal;
        font-size: 0.16rem;
        text-align: center;
        position: relative;
        background: @bar-color;
        .active {
            color: @theme-red;
        }
        .top-menu-bar {
            width: calc(~"100% - 0.4rem");
            position: absolute;
            
        }
        .top-bar-active-active {
            top: 0.1rem;
        }
        .top-menu-more-btn {
            position: absolute;
            right: 0;top: 0;
            width: 0.4rem;
            height: 0.4rem;
            background: @bar-color;
            z-index: 9;
            .list-shadow {
                position: absolute;
                right: 100%;top: 0;
                width: 0.1rem;
                height: 100%;
                background: url(data:image/png;base64,) no-repeat center right;
                background-size: contain;
            }
            .cross {
                width: 0.17rem;
                height: 0.17rem;
                &:before, &:after {
                    position: absolute;
                    content: ' ';
                    height: 0.17rem;
                    width: 0.04rem;
                    background-color: @theme-red;
                }
                &:before {
                    transform: rotate(90deg);
                }
            }
        }
    }
</style>


